<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-company-general-documents',
  data () {
    return {
      config: {
        title: 'Dokumen Perusahaan',
        model_api: 'company-general-documents',
        getter: 'company-general-documents',
        setter: 'company-general-documents',
        pk_field: null,
        permission: {
          create: 'create-company-general-documents',
          read: 'view-company-general-documents',
          update: 'update-company-general-documents',
          delete: 'delete-company-general-documents'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          {
            id: 'document_type_id',
            label: 'Tipe Dokumen',
            methods: {
              create: {
                setter: 'master-document-types',
                getter: 'master-document-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-document-types',
                getter: 'master-document-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-document-types',
                getter: 'master-document-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'file_filename',
            label: 'File Dokumen',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'file_filename.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }],
                view_data: 'file_filename'
              }
            }
          }
        ]
      }
    }
  }
}
</script>



